import React from "react";

const Photo = props => (
    <div>
        <h3>{props.photo.title}</h3>
        <h3>{props.photo.date}</h3>
        <img className="img-fluid my-2" src={props.photo.url} alt={props.photo.title} />
        <p className="my-3">{props.photo.explanation}</p>
        <br />
    </div>
);

export default Photo;